.Circle {
    margin: 20px auto;
    border: 10px solid #45aaf2;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Clock {
    color: #25ccf7;
    font-size: 5rem;
    width: 300px;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.BtnWrapper {
    padding-left: 0.5rem;
}
.btn {
    font-size: 2rem;
    background-color: transparent;
    outline: none;
    border: none;
    display: inline-block;
    cursor: pointer;
    color: #45aaf2;
}
.btn:hover {
    color: #d1d8e0;
    transition: 0.3s ease-in;
}

@media (max-width: 540px) {
    .Circle {
        width: 200px;
        height: 200px;
    }
    .Clock {
        font-size: 3rem;
    }
    .btn {
        font-size: 1.5rem;
    }
}
